export default async function ContractsPage({ params }: { params: Promise<{ id: string }> }) {
  const { id } = await params;
  return (
    <div className="space-y-4">
      <h3 className="text-lg font-semibold">Контракты и события — {id}</h3>
      <p>Загрузка ABI, регистрация в FireFly, создание listeners, просмотр событий.</p>
    </div>
  );
}

